body {
  box-sizing: content-box !important; }

*,
*:before,
*:after {
  box-sizing: inherit !important; }

.f-checkbox--outlined .f-checkbox__field:not([disabled]) + .f-checkbox__label:after {
  color: #6fcf97; }

.f-checkbox--outlined .f-checkbox__field:not([disabled]):hover + .f-checkbox__label:after {
  color: #6fcf97; }

.f-checkbox--outlined .f-checkbox__field:not([disabled]):focus + .f-checkbox__label:after {
  color: #6fcf97; }

.f-checkbox--outlined .f-checkbox__field:not([disabled]):checked + .f-checkbox__label:before {
  color: #6fcf97; }

.f-checkbox--outlined .f-checkbox__field:not([disabled]):hover + .f-checkbox__label:before {
  color: #6fcf97; }

.f-checkbox--outlined .f-checkbox__field:not([disabled]):focus + .f-checkbox__label:before {
  color: #6fcf97; }

.f-checkbox--outlined .f-checkbox__field[disabled] + .f-checkbox__label:before {
  color: gainsboro; }

.f-checkbox--outlined .f-checkbox__message {
  margin: 0.5em 0 0 0.5em;
  margin-left: 0; }
